$bg:rgb(16,16,16);
.banner{
    height: 600px;
    background-color: rgb(16,16,16);
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 3rem;
    box-sizing: border-box;

    &::after{
        content: none;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0%;
        left: 0%;
        background-color: $bg;
        opacity: 0.4;
    }

    h1,p{
        color: white;
        font-size: 3rem;
        width: 50%;
        z-index: 10;
    }
    p{
        font-size: 1.2rem;
        letter-spacing: 2px;
        word-spacing: 3px;
        font-weight: 100px; 
    }
    >div{
         display: flex;

         button{
            padding: 1rem;
            width: 150px;
            outline: none;
            border: none;
            margin: 1rem;
            z-index: 10;
            border-radius: 5px;
            font-size: 1.2rem;
            cursor: pointer;
            
            background-color: white;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all 0.5s;

            &:hover{
                opacity: 0.6;
            }

            &:nth-child(odd)
            {
                background-color: white;
                color: black;
            }
            &:nth-child(even)
            {
                background-color: rgb(125, 123, 123);
                color: rgb(237, 237, 237);
            }
            svg{
                font-size: 1.7rem;
                margin: 0 10px;
            }
        }
    }
    


}
.row{
    background-color: $bg;
    padding: 1rem;
    >h2{
        font-weight: 400;
        margin: 0.3rem; 
    }
    >div{
        display: flex;
        
        overflow-x: auto;
        
    }
}

.card{
    width: 200px;
    margin: 0.3rem;
    cursor: pointer;
    transition: transform 0.5s;
    &:hover{
        transform: translateY(-10px)scale(1.05) ;
    }
}

.genreBox{
    overflow-x: auto;
    display: flex;
    height: 100px;
    background-color: $bg;
    flex: 0%;
    >a{
        color: black;
        background-color: white;
        padding: 1rem 0;
        text-align: center;
        border-radius: 5px;
        transition: all 0.5s;
        min-width: 10rem;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 1rem;
        &:hover{
            background-color: rgb(154, 152, 152);
        }
    }
}
